import React, {useEffect, useState} from "react";
import styles from "./Products.module.css";
import Product from "../product/Product";
import axios from "axios";
import './trans.css'
import {CSSTransition, TransitionGroup} from "react-transition-group";
import MySpinner from "../spinner/MySpinner";

export default function Products() {
    const [products, setProducts] = useState([]);
    const [isLoading, setIsLoading] = useState(null);
    const [isLoadingInside, setIsLoadingInside] = useState(null);
    const [name, setName] = useState('');
    const [expiration, setExpiration] = useState('');
    const [amount, setAmount] = useState('');
    const [id, setId] = useState(null);
    const [idForDel, setIdForDel] = useState(0);
    const [AddOrEdit, setAddOrEdit] = useState(false);

    const AXIOS = axios.create({
        baseURL: 'api',
        headers: {
            Authorization: `Bearer ${JSON.parse(localStorage.getItem('token'))}`
        }
    })

    const fetchProducts = async () => {
        if (localStorage.getItem('token')) {
            try {
                setIsLoading(true)
                await AXIOS.get('products').then(results => {
                    if (results.data != null) {
                        setProducts(results.data);
                        setIsLoading(false);
                    } else {
                        setIsLoading(false);
                    }
                })
            } catch (e) {
                console.error(e);
            }
        }
    }

    const addProduct = async (e) => {
        e.preventDefault();
        const data = {
            name: name,
            expiration: expiration,
            amount: amount
        }
        if (localStorage.getItem('token')) {
            try {
                setIsLoadingInside(true);
                await AXIOS.post('products', data).then((results => {
                    if (results.data != null) {
                        setProducts(prev => new Array(1).fill(results.data).concat(prev));
                        setIsLoadingInside(false);
                    } else {
                        setIsLoadingInside(false);
                    }
                })).then(v => {
                    setName('');
                    setExpiration('');
                    setAmount('')
                })
            } catch (e) {
                console.log(e + ' exception');
            }
        }
    }

    const cancel = (e) => {
        e.preventDefault();
        setName('');
        setExpiration('');
        setAmount('');
        setAddOrEdit(false)

    }
    const edit = (item) => {
        setName(item.name);
        setExpiration(item.expiration);
        setAmount(item.amount);
        setId(item.id);
        setAddOrEdit(true);
        window.scrollTo(0, 0)
    }

    const editProd = async (e) => {
        e.preventDefault();
        const data = {
            name: name,
            expiration: expiration,
            amount: amount,
            id: id
        }
        try {
            setIsLoadingInside(true)
            const newProducts = [...products];
            AXIOS.put('products/edit', data).then(res => {
                if (res.data != null) {
                    newProducts.forEach((e, index) => {
                        if (e.id === res.data.id) {
                            newProducts[index] = res.data;
                        }
                    })
                    setProducts(newProducts);
                    setName('');
                    setExpiration('');
                    setAmount('');
                    setAddOrEdit(false);
                    setIsLoadingInside(false);
                } else {
                    setIsLoadingInside(false);
                }
            })
        } catch (e) {
            console.error(e)
        }
    }
    const del = (item) => {
        setIdForDel(item.id);
    }

    const deleteProd = async () => {
        const newProducts = [...products];
        try {
            setIsLoadingInside(true)
            const data = {
                id: idForDel
            }
            await AXIOS.delete('products/delete', {data: data}).then(resp => {
                    if (resp.data != null) {
                        newProducts.forEach((e, index) => {
                            if (e.id === idForDel) {
                                newProducts.splice(index, 1);
                            }
                        })
                        setProducts(newProducts);
                        setIsLoadingInside(false);
                    } else {
                        setIsLoadingInside(false);
                    }
                }
            )
        } catch (e) {
            console.error(e);
        }
    }

    useEffect(() => {
                               // todo? render with component
        if (idForDel > 0) {
            console.log("effect deleting...")
            deleteProd().then(res => res);
        }
    }, [idForDel]);

    useEffect(() => {
        console.log(' effect fetch product')
        fetchProducts();
    }, [])

    return (
        isLoading ? MySpinner :
            <div className={styles.container}>
                <div className={styles.addProdCont}>
                    <form className="form card-body">
                        <div className="form-group">
                            <label htmlFor='name'>New product</label>
                            <input className="form-control my"
                                   id='name'
                                   type='text'
                                   placeholder="Input name"
                                   name="name"
                                   value={name}
                                   onChange={(e) => {
                                       setName(e.target.value)
                                   }}
                            />
                            <br/>
                            <input className="form-control my"
                                   id='expiration'
                                   type='number'
                                   placeholder="Input expiration (in days)"
                                   name="expiration"
                                   value={expiration}
                                   onChange={(e) => {
                                       setExpiration(e.target.value)
                                   }}
                            />
                            <br/>
                            <input className="form-control my"
                                   id='amount'
                                   type='number'
                                   placeholder="Input amount (how much)"
                                   name="amount"
                                   value={amount}
                                   onChange={(e) => {
                                       setAmount(e.target.value)
                                   }}
                            />
                            <TransitionGroup>

                                {!AddOrEdit ?
                                    <div className={styles.buttonAdd}>
                                        <button className={styles.navLink} onClick={addProduct}>Add</button>
                                    </div>
                                    :
                                    <CSSTransition
                                        in={AddOrEdit}
                                        timeout={500}
                                        classNames="example"
                                    >
                                        <section className={styles.buttonAdd2}>
                                            <button className={styles.navLinkCancel} onClick={cancel}>Cancel</button>
                                            <button className={styles.navLink} onClick={editProd}>Edit</button>
                                        </section>
                                    </CSSTransition>
                                }
                            </TransitionGroup>
                            {isLoadingInside ? MySpinner : <div></div>}
                        </div>
                    </form>

                </div>
                <div>
                    <TransitionGroup>
                        {
                            products.map((item, index) =>
                                <CSSTransition
                                    key={item.id}
                                    timeout={500}
                                    classNames="example"
                                >
                                    <Product item={item}
                                             edit={edit}
                                             del={del}
                                             key={item.id}
                                             index={index}
                                    />
                                </CSSTransition>
                            )
                        }
                    </TransitionGroup>
                </div>
            </div>
    );
}
